import Vuex from 'vuex';
import Vue from 'vue';

Vue.use(Vuex); // 이렇게 하면 뷰와 뷰엑스가 연결된다.

export const SET_WINNER = 'SET_WINNER';
export const CLICK_CELL = 'CLICK_CELL';
export const CHANGE_TURN = 'CHANGE_TURN';
export const RESET_GAME = 'RESET_GAME';
export const NO_WINNER = 'NO_WINNER';
// 만든 뮤테이션 함수들을 변수로 뺄 수가 있다.
// 이렇게 빼기 위해서는 함수 이름에 대괄호를 넣어야한다.
// 이렇게 만든 건 다른 파일에서 사용할 수 있다.

export default new Vuex.Store({
	state: {
		tableData: [
			['', '', '',],
			['', '', '',],
			['', '', '',],
		],
		turn: 'O',
		winner: '', // 통제할 데이터를 옮겨온다.
	}, // 뷰의 데이터와 비슷하다.
	getters: {
		turnMessage(state) {
			return state.turn + '님이 승리하셨습니다.';
		},
	}, // 컴퓨티드와 비슷하다.
	mutations: {
		[SET_WINNER](state, winner) {
			state.winner = winner;
		}, // 뮤테이션의 함수는 대문자인 것이 공식 문서의 추천 사항이다.
		// 첫번째 매개변수는 스테이트, 두번째 매개변수는 데이터.
		[CLICK_CELL](state, { row, cell }) {
			Vue.set(state.tableData[row], cell ,state.turn);
		},
		[CHANGE_TURN](state) {
			state.turn = state.turn === 'O' ? 'X' : 'O';
		},
		[RESET_GAME](state) {
			state.turn = 'O';
			state.tableData = [
				['', '', '',],
				['', '', '',],
				['', '', '',],
			];
		},
		[NO_WINNER](state) {
			state.winner = '';
		}
	}, // 스테이트를 수정할 때 사용.
	actions: {}, // 비동기 사용, 혹은 여러 뮤테이션을 실행할 때.
});