<template>
	<div>
		<div>{{turn}}님의 턴입니다.</div>
		<table-component>
			<tr v-for="(rowData, rowIndex) in tableData" :key="rowIndex">
				<td @click="onClickTd(rowIndex, cellIndex)" v-for="(cellData, cellIndex) in rowData" :key="cellIndex">{{cellData}}</td>
			</tr>
		</table-component>
		<div v-if="winner">{{winner}}님의 승리!</div>
	</div>
</template>

<script>
	import { mapState } from 'vuex';
	import store, { CHANGE_TURN, CLICK_CELL, NO_WINNER, RESET_GAME, SET_WINNER } from './store';
	// 메인 컴포넌트에서 스토어를 불러와주고
	import TableComponent from './TableComponent';

	export default {
		store, // 불러온 스토어를 사용할 수 있도록 명시한다.
		components: {
			TableComponent,
		}, // 자식 컴포넌트가 있을 경우에 여기에 적는다.
		computed: {
			...mapState(['winner', 'turn', 'tableData']),
		}, // 가공되는 데이터들은 여기에 넣어서 사용하는 것이 좋다. 캐싱 때문이다.
		methods: {
			onClickTd(rowIndex, cellIndex) {
				if (this.cellData) {
					return;
				} // 값이 있는 자리를 눌렀을 경우 중단.

				// this.$root.$data.tableData[this.rowIndex][this.cellIndex] = this.$root.$data.turn;
				// 이렇게 하면 값이 변한다. 하지만 렌더링은 되지 않는다.
				// 배열이나 객체의 내부를 인덱스를 통한 변화는 렌더링되지 않는다. 그래서 아래처럼 해야한다.
				// $set은 이러한 뷰의 한계를 보완해준다.
				// const rootData = this.$root.$data;
				// this.$set(rootData.tableData[this.rowIndex], this.cellIndex, rootData.turn);

				this.$store.commit(CLICK_CELL, { row: rowIndex, cell: cellIndex });
				// 스토어에서 뮤테이션을 부른다. 설정한 인수는 꼭 불러줘야한다.

				let win = false;
				if (this.tableData[rowIndex][0] === this.turn &&
					this.tableData[rowIndex][1] === this.turn &&
					this.tableData[rowIndex][2] === this.turn) {
					win = true;
				}
				if (this.tableData[0][cellIndex] === this.turn &&
					this.tableData[1][cellIndex] === this.turn &&
					this.tableData[2][cellIndex] === this.turn) {
					win = true;
				}
				if (this.tableData[0][0] === this.turn &&
					this.tableData[1][1] === this.turn &&
					this.tableData[2][2] === this.turn) {
					win = true;
				}
				if (this.tableData[0][2] === this.turn &&
					this.tableData[1][1] === this.turn &&
					this.tableData[2][0] === this.turn) {
					win = true;
				}

				if (win) { // 이겼을 때.
					this.$store.commit(SET_WINNER, this.turn);
					this.$store.commit(RESET_GAME);
				} else { // 무승부였을 때.
					let all = true;
					this.tableData.forEach((row) => {
						row.forEach((cell) => {
							if (!cell) {
								all = false;
							}
						});
					});

					if (all) {
						this.$store.commit(NO_WINNER);
						this.$store.commit(RESET_GAME);
					}
					this.$store.commit(CHANGE_TURN);
				}
			},
		},
	};
</script>

<style>
	table {
		border-collapse: collapse;
	}

	td {
		border: 1px solid #333333;
		width: 50px;
		height: 50px;
		text-align: center;
	}
</style>